/* Navbar.css */

.navbar-container {
  background-color: rgba(255,255,255,0.9);
  border-bottom: 1px solid var(--bh-border);
  backdrop-filter: blur(6px);
  box-shadow: var(--bh-shadow-xs);
  position: sticky;
  top: 0;
  z-index: 100;
  transition: all var(--bh-transition);
}

.navbar-wrapper {
  max-width: 1200px;
  margin: 0 auto;
  padding: 1rem 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navbar-left {
  display: flex;
  align-items: center;
  gap: 2rem;
}

.navbar-logo {
  font-size: 1.45rem;
  font-weight: 800;
  background: var(--bh-gradient-1);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  letter-spacing: -0.5px;
}

.navbar-links {
  display: flex;
  gap: 0.5rem;
  align-items: center;
}

.navbar-link {
  padding: 0.45rem 0.8rem;
  font-size: 0.95rem;
  font-weight: 600;
  color: var(--bh-text-secondary);
  background: transparent;
  border-radius: 0.5rem;
  transition: all var(--bh-transition);
}

.navbar-link:hover {
  color: var(--bh-text-primary);
  background-color: rgba(102,126,234,0.06);
}

.navbar-right {
  display: flex;
  gap: 1rem;
  align-items: center;
}

/* theme toggle removed - light-only UI */

.navbar-btn {
  padding: 0.6rem 1.2rem;
  font-size: 0.9rem;
  font-weight: 600;
  border-radius: 0.5rem;
  transition: all 0.3s ease;
  animation: slideInRight 0.4s ease;
}

.navbar-btn-primary {
  background: var(--bh-gradient-1);
  color: white;
  box-shadow: var(--bh-shadow-md);
}

.navbar-btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: var(--bh-shadow-lg);
}

.navbar-btn-secondary {
  background-color: var(--bh-bg);
  color: var(--bh-text-primary);
  border: 1px solid var(--bh-border);
}

.navbar-btn-secondary:hover {
  background-color: rgba(102,126,234,0.04);
  border-color: rgba(102,126,234,0.12);
}

/* Search box + dropdown helpers */
.navbar-search { display:flex; align-items:center; gap:.5rem; background:var(--bh-bg-card); border:1px solid var(--bh-border); padding:.35rem .6rem; border-radius:8px; box-shadow:var(--bh-shadow-xs); }
.navbar-search input { border:0; outline:none; font-size:.95rem; color:var(--bh-text-primary); background:transparent; }
.nav-dropdown { position:relative; }
.nav-dropdown .dropdown-menu { position:absolute; top:calc(100% + .5rem); left:0; min-width:200px; background:var(--bh-bg-card); border:1px solid var(--bh-border); box-shadow:var(--bh-shadow-md); border-radius:8px; padding:.5rem; display:none; z-index:130; }
.nav-dropdown:hover .dropdown-menu { display:block; }
.dropdown-item { padding:.5rem .75rem; border-radius:6px; font-size:.95rem; color:var(--bh-text-primary); }
.dropdown-item:hover { background: rgba(102,126,234,0.04); }

@media (max-width: 768px) {
  .navbar-links {
    display: none;
  }
  
  .navbar-wrapper {
    padding: 1rem 0.5rem;
  }

  .navbar-left {
    gap: 0.5rem;
  }
}
